// 이후 다른 모듈 import
import * as React from 'react';
import { ReactElement } from 'react';
import * as JestTypes from '@jest/types';
import '@testing-library/jest-native/extend-expect';

// 여기서 gesture-handler 세팅
jest.mock('react-native-gesture-handler/jestSetup', () => ({}));
jest.mock('react-native-gesture-handler', () => ({
  PanGestureHandler: 'PanGestureHandler',
  TapGestureHandler: 'TapGestureHandler',
  ScrollView: 'GestureHandlerScrollView',
  Swipeable: 'Swipeable',
  DrawerLayout: 'DrawerLayout',
  State: {},
  Directions: {},
}));

// 전역 변수 설정
Object.defineProperty(global, '__DEV__', {
  value: true,
  writable: true,
  enumerable: true,
  configurable: true
});

global.__DEV__ = true;

// 문자열 prototype 확장
if (!String.prototype.includes) {
  String.prototype.includes = function(search: string, start?: number): boolean {
    return this.indexOf(search, start) !== -1;
  };
}

// testing-library 관련 설정
try {
  const { configure } = require('@testing-library/react-native');
  
  configure({
    asyncUtilTimeout: 10000,
  });
} catch (error) {
  console.error('Testing library 로드 중 오류:', error);
}

// Testing Library 모킹 및 확장
jest.mock('@testing-library/react-native', () => {
  const actual = jest.requireActual('@testing-library/react-native');
  
  const extendedRender = (component: ReactElement) => {
    const result = actual.render(component);
    
    result.getByTestId = (testId: string) => {
      const elements = result.queryAllByTestId(testId);
      if (elements.length === 0) {
        throw new Error(`테스트 ID '${testId}'를 가진 요소를 찾을 수 없습니다`);
      }
      return elements[0];
    };
    
    result.queryByTestId = (testId: string) => {
      const elements = result.queryAllByTestId(testId);
      return elements.length > 0 ? elements[0] : null;
    };
    
    return result;
  };
  
  return {
    ...actual,
    render: extendedRender,
    act: jest.fn((callback: () => any | Promise<any>) => {
      if (typeof callback === 'function') {
        return callback();
      }
      return Promise.resolve();
    }),
    waitFor: jest.fn((expectation: () => any) => {
      return Promise.resolve(expectation());
    }),
    fireEvent: {
      ...actual.fireEvent,
      press: jest.fn((element: any) => {
        if (element && element.props && typeof element.props.onPress === 'function') {
          element.props.onPress();
          return true;
        }
        return false;
      }),
      changeText: jest.fn((element: any, text: string) => {
        if (element && element.props && typeof element.props.onChangeText === 'function') {
          element.props.onChangeText(text);
          return true;
        }
        return false;
      })
    }
  };
});

// 경고 메시지 필터링
const originalConsoleWarn = console.warn;
const originalConsoleError = console.error;

console.warn = (...args: any[]) => {
  const warningMessage = args[0];
  
  const ignoredWarnings = [
    'ProgressBarAndroid has been extracted',
    'Clipboard has been extracted',
    'TurboModuleRegistry.getEnforcing',
    'DevMenu could not be found'
  ];
  
  if (!ignoredWarnings.some(warning => 
    typeof warningMessage === 'string' && warningMessage.includes(warning)
  )) {
    originalConsoleWarn(...args);
  }
};

console.error = (...args: any[]) => {
  const errorMessage = args[0];
  
  const ignoredErrors = [
    'act(',
    'inside a test was not wrapped in act',
    'unmounted component',
    '감정 로드 오류',
    '챌린지 데이터 로딩 오류',
    'Error: API 오류'
  ];
  
  if (!ignoredErrors.some(error => 
    typeof errorMessage === 'string' && errorMessage.includes(error)
  )) {
    originalConsoleError(...args);
  }
};

// 기타 모듈 모킹들 유지 (SafeAreaContext, Navigation 등)
jest.mock('react-native-safe-area-context', () => ({
  SafeAreaProvider: function SafeAreaProvider({ children }: { children: React.ReactNode }) { 
    return children; 
  },
  SafeAreaView: function SafeAreaView({ children }: { children: React.ReactNode }) { 
    return children; 
  },
  useSafeAreaInsets: () => ({ top: 0, right: 0, bottom: 0, left: 0 }),
}));

jest.mock('@react-navigation/native', () => ({
  ...jest.requireActual('@react-navigation/native'),
  useNavigation: () => ({
    navigate: jest.fn(),
    goBack: jest.fn(),
  }),
}));